"use client";

import Link from "next/link";
import { useRef } from "react";
import { useGSAP } from "@gsap/react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { getLandingImage } from "@/utils/landingImages";

gsap.registerPlugin(ScrollTrigger);

const RECOVERY_PROGRAMS = [
  {
    title: "줄기세포 재생",
    description: "자체 지방 유래 줄기세포를 활용한 피부 재생 프로그램으로 콜라겐과 엘라스틴 생성을 촉진합니다.",
  },
  {
    title: "프리미엄 스킨부스터",
    description: "고농도 영양 성분을 피부 깊숙이 전달하여 피부 장벽을 강화하고 수분을 공급합니다.",
  },
  {
    title: "레이저 재생",
    description: "비침습 레이저로 피부 재생을 촉진하고 모공과 잔주름을 개선합니다.",
  },
  {
    title: "종합 관리",
    description: "개인의 피부 상태에 맞춘 맞춤형 재생 프로그램으로 최적의 결과를 도출합니다.",
  },
];

const BENEFITS = [
  "피부 탄력 및 주름 개선",
  "모공 및 잔주름 완화",
  "피부 톤 개선 및 윤기 회복",
  "피부 장벽 강화",
  "자연스러운 피부 재생",
];

export default function RecoveryView() {
  const heroRef = useRef<HTMLElement>(null);
  const introRef = useRef<HTMLElement>(null);
  const programRef = useRef<HTMLElement>(null);
  const benefitRef = useRef<HTMLElement>(null);

  useGSAP(
    () => {
      if (!introRef.current) return;
      gsap.fromTo(
        introRef.current,
        { opacity: 0, y: 40 },
        {
          opacity: 1,
          y: 0,
          duration: 0.9,
          ease: "power3.out",
          scrollTrigger: {
            trigger: introRef.current,
            start: "top 82%",
            toggleActions: "play none none reverse",
          },
        }
      );
    },
    { dependencies: [] }
  );

  useGSAP(
    () => {
      const cards = programRef.current?.querySelectorAll(".program-card");
      cards?.forEach((el, i) => {
        gsap.fromTo(
          el,
          { opacity: 0, y: 50 },
          {
            opacity: 1,
            y: 0,
            duration: 0.6,
            delay: i * 0.1,
            ease: "power3.out",
            scrollTrigger: {
              trigger: programRef.current,
              start: "top 70%",
              toggleActions: "play none none reverse",
            },
          }
        );
      });
    },
    { scope: programRef, dependencies: [] }
  );

  return (
    <main className="min-h-screen pt-[var(--header-height)] lg:pt-[var(--header-h)]">
      {/* Hero */}
      <section
        ref={heroRef}
        className="relative min-h-[90vh] flex items-center justify-center overflow-hidden bg-gradient-to-br from-[#f5f4f2] via-[#fafaf8] to-[#f5f4f2]"
      >
        <div
          className="absolute inset-0 bg-cover bg-center opacity-20"
          style={{
            backgroundImage: "url(https://images.unsplash.com/photo-1494790108377-be9c29b29330?q=80&w=2560&auto=format&fit=crop)",
          }}
        />
        <div className="relative z-10 text-center px-6 max-w-4xl mx-auto">
          <h1 className="font-[family-name:var(--font-en-serif)] text-[clamp(48px,8vw,96px)] font-light tracking-wide text-[#111] mb-6">
            RECOVERY
          </h1>
          <p className="text-[clamp(18px,2.5vw,24px)] text-[#666] leading-relaxed">
            피부 재생과 회복을 위한<br />
            종합 케어 프로그램
          </p>
        </div>
      </section>

      {/* Intro */}
      <section
        ref={introRef}
        className="relative border-t border-[#e8e8e8] bg-white px-6 py-16 lg:px-[var(--pad-global)] lg:py-24"
      >
        <div className="mx-auto max-w-[720px] text-center">
          <h2 className="font-[family-name:var(--font-en-serif)] text-[clamp(20px,2.5vw,26px)] tracking-[0.06em] text-[#111]">
            Recovery
          </h2>
          <p className="mt-4 text-[15px] leading-relaxed text-[#666]">
            RECOVERY는 피부 재생과 회복을 위한 종합 케어 프로그램입니다.
            줄기세포 재생, 프리미엄 스킨부스터, 레이저 재생 등을 통해 피부의 자연스러운 재생 능력을 촉진하고,
            탄력, 주름, 모공, 톤 등 전반적인 피부 상태를 개선합니다.
          </p>
        </div>
      </section>

      {/* Programs */}
      <section
        ref={programRef}
        className="hip-grain relative bg-[var(--hip-bg)] px-6 py-20 lg:px-[var(--pad-global)] lg:py-28"
      >
        <div className="mx-auto max-w-[1200px]">
          <h2 className="font-[family-name:var(--font-en-serif)] text-center text-[clamp(32px,4vw,48px)] tracking-wide text-[#111] mb-16">
            Recovery Programs
          </h2>
          <div className="grid md:grid-cols-2 gap-8">
            {RECOVERY_PROGRAMS.map((program, idx) => (
              <div
                key={idx}
                className="program-card bg-white p-8 rounded-lg shadow-sm border border-[#e8e8e8]"
              >
                <h3 className="text-xl font-medium text-[#111] mb-3">{program.title}</h3>
                <p className="text-[15px] leading-relaxed text-[#666]">{program.description}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Benefits */}
      <section
        ref={benefitRef}
        className="relative bg-white px-6 py-20 lg:px-[var(--pad-global)] lg:py-28"
      >
        <div className="mx-auto max-w-[800px]">
          <h2 className="font-[family-name:var(--font-en-serif)] text-center text-[clamp(32px,4vw,48px)] tracking-wide text-[#111] mb-12">
            Benefits
          </h2>
          <ul className="space-y-6">
            {BENEFITS.map((benefit, idx) => (
              <li key={idx} className="flex items-start gap-4">
                <span className="text-[var(--hip-accent)] text-xl mt-1">•</span>
                <span className="text-[16px] leading-relaxed text-[#666] flex-1">{benefit}</span>
              </li>
            ))}
          </ul>
        </div>
      </section>

      {/* CTA */}
      <section className="relative bg-[#1a1a1a] px-6 py-20 lg:px-[var(--pad-global)] lg:py-28">
        <div className="mx-auto max-w-[600px] text-center">
          <h2 className="font-[family-name:var(--font-en-serif)] text-[clamp(32px,4vw,48px)] text-white mb-6">
            상담 및 예약
          </h2>
          <p className="text-white/80 mb-8 leading-relaxed">
            RECOVERY 프로그램에 대한 자세한 상담을 원하시면<br />
            전문 상담사가 도와드리겠습니다.
          </p>
          <Link
            href="/community/contact"
            className="inline-block px-8 py-4 bg-white text-[#111] rounded-sm hover:bg-[var(--hip-accent)] hover:text-white transition-colors"
          >
            상담 예약하기
          </Link>
        </div>
      </section>
    </main>
  );
}
